import React from 'react';
import { Link } from 'react-router-dom';

function Badge({ children, color = 'var(--accent)' }) {
  return (
    <span style={{ fontSize: 11, background: color, color: '#fff', padding: '2px 6px', borderRadius: 999, marginLeft: 8 }}>
      {children}
    </span>
  );
}

function Games() {
  return (
    <div className="card" style={{ padding: 0 }}>
      {/* Bannière info développement */}
      <div style={{ padding: '10px 12px', background: 'var(--accent-soft)', borderBottom: '1px solid var(--line)' }}>
        <strong>Jeux en développement</strong>
        <div style={{ fontSize: 13, color: 'var(--muted)' }}>
          Ces jeux évoluent en continu. Vos idées sont les bienvenues — proposez des améliorations dans le chat ou auprès des admins.
        </div>
      </div>

      <div style={{ padding: 12 }}>
        <h2 style={{ marginTop: 0 }}>Hub des jeux</h2>
        <p style={{ color: 'var(--muted)', marginTop: -6 }}>Choisissez un jeu pour commencer. Certains modes sont expérimentaux.</p>

        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(260px, 1fr))', gap: 16, marginTop: 16 }}>
          <div className="card" style={{ display:'flex', flexDirection:'column', gap:8 }}>
            <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
              <h3 style={{ margin: 0 }}>Morpion</h3>
              <div>
                <Badge>Local</Badge>
              </div>
            </div>
            <p style={{ color: 'var(--muted)', marginTop: 0 }}>Classique 3×3, 2 joueurs (X/O) local</p>
            <Link className="week-btn" to="/games/morpion">Jouer</Link>
          </div>

          <div className="card" style={{ display:'flex', flexDirection:'column', gap:8 }}>
            <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
              <h3 style={{ margin: 0 }}>Morpion en ligne</h3>
              <div>
                <Badge color="#7c3aed">En ligne</Badge>
              </div>
            </div>
            <p style={{ color: 'var(--muted)', marginTop: 0 }}>Créez ou rejoignez une salle (2 joueurs)</p>
            <Link className="week-btn" to="/games/morpion-online">Jouer en ligne</Link>
          </div>

          <div className="card" style={{ display:'flex', flexDirection:'column', gap:8 }}>
            <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
              <h3 style={{ margin: 0 }}>Flappy Bird</h3>
              <div>
                <Badge color="#f59e0b">Prototype</Badge>
              </div>
            </div>
            <p style={{ color: 'var(--muted)', marginTop: 0 }}>Version précoce</p>
            <Link className="week-btn" to="/games/flappybird">Ouvrir</Link>
          </div>

          <div className="card" style={{ display:'flex', flexDirection:'column', gap:8 }}>
            <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
              <h3 style={{ margin: 0 }}>MMORPG</h3>
              <div>
                <Badge color="#06b6d4">Alpha</Badge>
                <Badge color="#7c3aed">En ligne</Badge>
              </div>
            </div>
            <p style={{ color: 'var(--muted)', marginTop: 0 }}>Guildes, ville, exploration basique</p>
            <Link className="week-btn" to="/games/mmorpg">Entrer</Link>
          </div>

          <div className="card" style={{ display:'flex', flexDirection:'column', gap:8 }}>
            <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
              <h3 style={{ margin: 0 }}>Jeux HTML</h3>
              <div>
                <Badge color="#64748b">Web</Badge>
              </div>
            </div>
            <p style={{ color: 'var(--muted)', marginTop: 0 }}>Chargés depuis /public/games</p>
            <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
              <Link className="week-btn" to="/games/web/menteur">Menteur (HTML)</Link>
              <Link className="week-btn" to="/games/web/hexwar">Hexwar (HTML)</Link>
              <Link className="week-btn" to="/games/web/pixco">Pixco (HTML)</Link>
            </div>
          </div>

          <div className="card" style={{ display:'flex', flexDirection:'column', gap:8 }}>
            <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
              <h3 style={{ margin: 0 }}>Jeux de cartes</h3>
              <div>
                <Badge color="#f97316">Alpha</Badge>
              </div>
            </div>
            <p style={{ color: 'var(--muted)', marginTop: 0 }}>Prototype local (PvE basique)</p>
            <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
              <Link className="week-btn" to="/games/card-battle">Card Battle</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Games;